:root {
  --color-system-black: #000000;
  --color-system-white: #ffffff;
  --color-system-blue: #007aff;
  --color-system-gray-03: #7f7f7f;

  --color-map-search-scope: #ff766f;
  --color-map-green-selected: #4f9661;
  --color-map-green-dark: #61a166;
  --color-map-green-icon: #34c759;
  --color-map-green-default: #71d88b;
  --color-map-blue-default: #4da2ff;

  --chakra-color-gray-300: #cbd5e0;
  --chakra-color-gray-100: #edf2f7;

  --color-bg-primary-light: #f2f2f7;
  --color-bg-primary-dark: #313655;

  --color-chakra-white: #edf2f7;
  --color-chakra-black: #242a36;

  --color-bg-secondary-light: var(--color-system-white);
  --color-bg-secondary-dark: #515780;

  --color-floor-option-bg-opacity-light: #4d525c;
  --color-floor-option-bg-opacity-dark: #c8cdd4;

  --color-bg-select-light: #242a36cf;
  --color-bg-select-dark: #edf2f7d3;

  --color-input-placeholder-light: #edf2f7a6;
  --color-input-placeholder-dark: #242a366a;

  --color-map-layout-background-light: #4e4e4eff;
  --color-map-layout-background-dark: #363636ff;

  --color-map-layout-stroke: var(--color-system-gray-03);

  --color-map-layout-fill-light: var(--color-system-white);
  --color-map-layout-fill-dark: #1a202c;

  --dropshadow-map-layout-fancy-on: drop-shadow(2px 2px 4px rgba(0, 0, 0, 0.5));
  --dropshadow-map-layout-fancy-off: "";

  --background-filter-fancy-on: blur(5px);
  --background-filter-fancy-off: "";

  --transition-drawer-fancy-on: left 0.4s ease;
  --transition-drawer-fancy-off: "";

  --transition-drawer-overlay-fancy-on: all ease 0.4s;
  --transition-drawer-overlay-fancy-off: "";

  --color-drawer-bg-light: var(--color-system-white);
  --color-drawer-bg-dark: #2d3748;

  --color-button-bg-dark: rgba(255, 255, 255, 0.08);
  --color-button-bg-light: var(--chakra-color-gray-100);

  --color-button-bg-active-dark: rgba(255, 255, 255, 0.24);
  --color-button-bg-active-light: var(--chakra-color-gray-300);

  --color-button-bg-hover-dark: rgba(255, 255, 255, 0.37);
  --color-button-bg-hover-light: var(--chakra-color-gray-300);

  --margin-top: 19px;
}

html[data-theme="dark"] {
  --color-bg-select: var(--color-bg-select-dark);
  --color-input-placeholder: var(--color-input-placeholder-dark);
  --color-floor-option-bg-opacity: var(--color-floor-option-bg-opacity-dark);
  --color-map-layout-background: var(--color-map-layout-background-dark);
  --color-map-layout-fill: var(--color-map-layout-fill-dark);
  --color-drawer-bg: var(--color-drawer-bg-dark);
  --color-button-bg: var(--color-button-bg-dark);
  --color-button-bg-active: var(--color-button-bg-active-dark);
  --color-button-bg-hover: var(--color-button-bg-hover-dark);
  --color-soft: var(--color-chakra-black);
  --color-soft-alternate: var(--color-chakra-white);
  color-scheme: dark;
}

html[data-theme="light"] {
  --color-floor-option-bg-opacity: var(--color-floor-option-bg-opacity-light);
  --color-bg-select: var(--color-bg-select-light);
  --color-input-placeholder: var(--color-input-placeholder-light);
  --color-map-layout-background: var(--color-map-layout-background-light);
  --color-map-layout-fill: var(--color-map-layout-fill-light);
  --color-drawer-bg: var(--color-drawer-bg-light);
  --color-button-bg: var(--color-button-bg-light);
  --color-button-bg-active: var(--color-button-bg-active-light);
  --color-button-bg-hover: var(--color-button-bg-hover-light);
  --color-soft: var(--color-chakra-white);
  --color-soft-alternate: var(--color-chakra-black);
  color-scheme: light;
}

html[data-fancy="true"] {
  --colorMapLayoutdropShadowFancy: var(--dropshadow-map-layout-fancy-on);
  --background-filter-fancy: var(--background-filter-fancy-on);
  --transition-drawer-fancy: var(--transition-drawer-fancy-on);
  --transition-drawer-overlay-fancy: var(--transition-drawer-overlay-fancy-on);
}

html[data-fancy="false"] {
  --colorMapLayoutdropShadowFancy: var(--dropshadow-map-layout-fancy-off);
  --background-filter-fancy: var(--background-filter-fancy-off);
  --transition-drawer-fancy: var(--transition-drawer-fancy-off);
  --transition-drawer-overlay-fancy: var(--transition-drawer-overlay-fancy-off);
}

.map-bg {
  stroke: var(--color-map-layout-stroke);
  fill: var(--color-map-layout-background);
}

.map-groups-stairs {
  fill: var(--color-map-layout-fill);
  pointer-events: none;
}
.map-groups-stairs polygon,
.map-groups-stairs polyline {
  stroke: var(--color-map-layout-stroke);
}
.map-groups-stairs g path:nth-child(1) {
  fill: var(--color-system-blue);
}
.map-groups-stairs g path:nth-child(2) {
  fill: var(--color-system-white);
}
.map-groups-stairs-fill {
  fill: var(--color-map-layout-background);
  pointer-events: none;
}
.map-groups-stairs-fill polygon,
.map-groups-stairs-fill polyline {
  stroke: var(--color-map-layout-stroke);
}
.map-groups-stairs-fill g path:nth-child(1) {
  fill: var(--color-system-blue);
}
.map-groups-stairs-fill g path:nth-child(2) {
  fill: var(--color-system-white);
}
.map-groups-gym path,
.map-groups-gym line,
.map-groups-gym polygon,
.map-groups-gym polyline {
  stroke: var(--color-map-layout-stroke) !important;
  fill: var(--color-map-blue-default);
}
.map-groups-gym-pole path,
.map-groups-gym-pole line,
.map-groups-gym-pole polygon {
  stroke: var(--color-map-layout-stroke);
  stroke-miterlimit: 10;
  stroke-width: 0.75;
  fill: none !important;
  filter: var(--colorMapLayoutdropShadowFancy);
  -webkit-filter: var(--colorMapLayoutdropShadowFancy);
}
.map-groups-rooms g line,
.map-groups-rooms g polygon,
.map-groups-rooms g polyline {
  stroke: var(--color-map-layout-stroke);
  fill: var(--color-map-blue-default);
  filter: var(--colorMapLayoutdropShadowFancy);
  -webkit-filter: var(--colorMapLayoutdropShadowFancy);
}
.map-groups-rooms g text,
.map-groups-rooms g span,
.map-groups-rooms g path {
  stroke: var(--color-system-white) !important;
  stroke-width: 0.1 !important;
  fill: var(--color-system-white) !important;
  color: var(--color-system-white) !important;
  font: 11px sans-serif;
  text-rendering: optimizeSpeed !important;
  pointer-events: none;
}
.map-groups-rooms g line:hover,
.map-groups-rooms g polygon:hover,
.map-groups-rooms g polyline:hover {
  opacity: 0.75;
  filter: var(--colorMapLayoutdropShadowFancy);
  -webkit-filter: var(--colorMapLayoutdropShadowFancy);
}
.map-groups-techs polygon,
.map-groups-techs polyline {
  stroke: var(--color-map-layout-stroke);
  fill: #bfbfbf;
  pointer-events: none;
  filter: var(--colorMapLayoutdropShadowFancy);
  -webkit-filter: var(--colorMapLayoutdropShadowFancy);
}
.map-groups-techs-icon path:nth-child(1) {
  fill: var(--color-system-blue);
}
.map-groups-techs-icon path:nth-child(2) {
  fill: var(--color-system-white);
}
.map-groups-wcs polyline {
  stroke: var(--color-map-layout-stroke);
  fill: #ac7ab6;
  pointer-events: none;
}
.map-groups-wcs-icon path:nth-child(1) {
  fill: var(--color-system-blue);
}
.map-groups-wcs-icon path:nth-child(2) {
  fill: var(--color-system-white);
}
.map-groups-escapes polygon,
.map-groups-escapes polyline {
  stroke: var(--color-map-layout-stroke);
  fill: #bfbfbf;
  pointer-events: none;
}
.map-groups-escapes-icon path:nth-child(1) {
  fill: var(--color-map-green-icon);
}
.map-groups-escapes-icon path:nth-child(2) {
  fill: var(--color-system-white);
}
.room-map-group-search-target,
.room-map-group-search-target line,
.room-map-group-search-target polygon,
.room-map-group-search-target polyline {
  fill: var(--color-map-search-scope) !important;
}
.map-groups-walls path,
.map-groups-walls polyline,
.map-groups-walls polygon,
.map-groups-walls rect,
.map-groups-walls line {
  stroke: var(--color-map-layout-stroke);
  fill: var(--color-map-layout-background);
}
.map-groups-void path,
.map-groups-void polyline,
.map-groups-void polygon,
.map-groups-void rect,
.map-groups-void line {
  stroke: var(--color-map-layout-stroke);
  fill: var(--color-map-layout-fill);
}
.map-groups-coworking-atameken path,
.map-groups-coworking-atameken polygon,
.map-groups-coworking-atameken polyline {
  stroke: var(--color-map-layout-stroke);
  fill: var(--color-map-layout-background);
  filter: var(--colorMapLayoutdropShadowFancy);
  -webkit-filter: var(--colorMapLayoutdropShadowFancy);
}
.map-label-huge {
  fill: var(--color-map-layout-stroke) !important;
  color: var(--color-map-layout-stroke) !important;
  stroke-width: 0.1 !important;
}
.map-label {
  fill: var(--color-system-white) !important;
  color: var(--color-system-white) !important;
  stroke-width: 0.1 !important;
}
.map-groups-rooms-vk g line,
.map-groups-rooms-vk g polygon,
.map-groups-rooms-vk g polyline {
  stroke: var(--color-map-layout-stroke);
  fill: var(--color-map-green-dark);
  filter: var(--colorMapLayoutdropShadowFancy);
  -webkit-filter: var(--colorMapLayoutdropShadowFancy);
}
.map-groups-rooms-vk g text,
.map-groups-rooms-vk g span,
.map-groups-rooms-vk g path {
  stroke: var(--color-system-white) !important;
  stroke-width: 0.1 !important;
  fill: var(--color-system-white) !important;
  color: var(--color-system-white) !important;
  font: 11px sans-serif !important;
  text-rendering: optimizeSpeed !important;
  pointer-events: none;
}
.map-fit-text {
  font: 9px sans-serif !important;
}

.aitu-spinner {
  -webkit-touch-action: none;
  touch-action: none;
  z-index: 3;
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -50px 0 0 -50px;
  width: 100px;
  height: 100px;
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
}

.ring__one {
  -webkit-touch-action: none;
  touch-action: none;

  animation-name: rotate-alternate;
  animation-duration: 2s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform-origin: center;

  -webkit-animation-name: rotate-alternate;
  -webkit-animation-duration: 2s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-transform-origin: center;
}

.ring__two {
  -webkit-touch-action: none;
  touch-action: none;

  animation-name: rotate;
  animation-duration: 3s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform-origin: center;

  -webkit-animation-name: rotate;
  -webkit-animation-duration: 3s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-transform-origin: center;
}

.ring__three {
  -webkit-touch-action: none;
  touch-action: none;

  animation-name: rotate-alternate;
  animation-duration: 6s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform-origin: center;

  -webkit-animation-name: rotate-alternate;
  -webkit-animation-duration: 6s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-transform-origin: center;
}

.ring__four {
  -webkit-touch-action: none;
  touch-action: none;

  animation-name: rotate;
  animation-duration: 12s;
  animation-timing-function: linear;
  animation-iteration-count: infinite;
  transform-origin: center;

  -webkit-animation-name: rotate;
  -webkit-animation-duration: 12s;
  -webkit-animation-timing-function: linear;
  -webkit-animation-iteration-count: infinite;
  -webkit-transform-origin: center;
}

@-webkit-keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }
  to {
    transform: rotate(-360deg) scale(1);
  }
}

@keyframes rotate {
  from {
    transform: rotate(0deg) scale(1);
  }
  to {
    transform: rotate(-360deg) scale(1);
  }
}

@-webkit-keyframes rotate-alternate {
  from {
    -webkit-transform: rotate(0deg) scale(1);
  }
  to {
    -webkit-transform: rotate(360deg) scale(1);
  }
}

@keyframes rotate-alternate {
  from {
    transform: rotate(0deg) scale(1);
  }
  to {
    transform: rotate(360deg) scale(1);
  }
}

.ring__four,
.ring__three,
.ring__two,
.ring__one path {
  -webkit-touch-action: none;
  touch-action: none;

  stroke: var(--color-soft-alternate);
  stroke-width: 4px;
  fill: none;
  stroke-linecap: round;
}

.ring_blue {
  -webkit-touch-action: none;
  touch-action: none;

  stroke: var(--color-map-blue-default);
}

.ring__one circle {
  -webkit-touch-action: none;
  touch-action: none;

  stroke: var(--color-soft-alternate);
  stroke-width: 5px;
  fill: none;
}

.ring__center {
  -webkit-touch-action: none;
  touch-action: none;

  stroke: var(--color-soft-alternate);
  stroke-width: 0px;
  fill: var(--color-soft-alternate);
}
